.program-card__catalog-link {
  display: inline-block;
  margin: 0;
  margin-bottom: 18px;
  position: relative;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  padding-right: 49px;
}
.program-card__catalog-link::before {
  content: "";
  position: absolute;
  right: 15px;
  top: calc(50% - 1px);
  width: 18px;
  height: 2px;
  background-color: #000000;
  z-index: 2;
}
.program-card__catalog-link::after {
  content: "";
  position: absolute;
  right: 8px;
  top: calc(50% - 5px);
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 7px solid #000000;
  z-index: 2;
}
.program-card__catalog-link:hover::before,
.program-card__catalog-link:active::before {
  width: 26px;
  right: 7px;
}
.program-card__catalog-link:hover::after,
.program-card__catalog-link:active::after {
  right: 0;
}
.program-card__catalog-link:active {
