.container {
  width: @container-phone-width;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: @container-tablet-width;
  }

  @media (min-width: @desktop-width) {
    width: @container-desktop-width;
  }
}

.container--phone-full-width {
  width: 100%;

  @media (min-width: @tablet-width) {
    width: @container-tablet-width;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: @container-desktop-width;
  }
}

.container--form {
  @media (min-width: @tablet-width) and (max-width: @tablet-width-only) {
    width: @tablet-width - 2*70px;
  }
}
